import { contentfulClient } from "App"
import * as types from "store/constants"

const loadingContentful = () => ({ type: types.LOADING_CONTENTFUL })

export const fetchEcommerceProducts = ({ category = "" } = {}) => (dispatch) => {
	dispatch(loadingContentful())
	return contentfulClient
		.getEntries({ content_type: "product", "fields.category": category })
		.then(({ items }) => {
			dispatch({ type: types.FETCH_ECOMMERCE_PRODUCTS, payload: items.map((item) => item.fields) })
		})
		.catch((err) => {
			dispatch({ type: types.FETCH_ECOMMERCE_PRODUCTS_ERROR })
			console.error(err)
		})
}

